.projects {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}
.projects img {
    width: 100%;
}
.card {
    box-shadow: 1px 2px 5px 0px rgb(189, 93, 56,.1);
    margin: 10px;
    -webkit-transition: all .3s ease-in-out;
    transition: all .3s ease-in-out;
}
.card:hover {
    transform: scale(1.1);
    box-shadow: 1px 2px 5px 0px rgb(189, 93, 56,.3);
}
.card-body {
    padding: 0px 10px 10px;
}
.card-body a {
    list-style: none;
    text-decoration: none;
    background-color: #ca7d60;
    color: white;
    border-radius: 5px;
    padding: 5px;
}
.card-body a:hover {
    background-color:  rgb(189, 93, 56);
}